"use client";

import { useState } from "react";
import { useAuth } from "@/context/AuthContext";
import { useRouter } from "next/navigation";

export default function ProfilePage() {
  const { login, register, user } = useAuth();
  const router = useRouter();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const [experience, setExperience] = useState("");

  // Login handler
  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const res = await fetch("/api/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      });
      const data = await res.json();
      if (res.ok) {
        login(data.user);
        setMessage("Login successful!");
      } else {
        setMessage(data.error || "Login failed");
      }
    } catch (err) {
      setMessage("Server error");
    }
  };

  // Register handler
  const handleRegister = async (e) => {
    e.preventDefault();
    try {
      const res = await fetch("/api/auth/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      });
      const data = await res.json();
      if (res.ok) {
        login(data.user);
        setMessage("Account created!");
      } else {
        setMessage(data.error || "Registration failed");
      }
    } catch {
      setMessage("Server error");
    }
  };

  // Save experience
  const handleSaveExperience = async () => {
    if (!user) {
      alert("Login to save your experience permanently.");
      return;
    }

    try {
      const res = await fetch("/api/save-experience", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ experience }),
      });

      const data = await res.json();
      if (res.ok) alert("Experience saved!");
      else alert(data.error || "Failed to save experience.");
    } catch {
      alert("Server error while saving experience.");
    }
  };

  return (
    <div style={{ padding: "20px", maxWidth: "500px", margin: "auto" }}>
      {/* If not logged in: Login + Register */}
      {!user && (
        <>
          <form onSubmit={handleLogin} style={{ marginBottom: "20px" }}>
            <h2>Login</h2>
            <label>Email:</label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
            <label>Password:</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
            <button type="submit">Login</button>
          </form>

          <form onSubmit={handleRegister} style={{ marginBottom: "20px" }}>
            <h2>Register</h2>
            <label>Email:</label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
            <label>Password:</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
            <button type="submit">Create Account</button>
          </form>
        </>
      )}

      {/* Experience selection (always visible) */}
      <div>
        <h2>Experience Level</h2>
        <select
          value={experience}
          onChange={(e) => setExperience(e.target.value)}
        >
          <option value="">Select your level</option>
          <option value="BEGINNER">Beginner</option>
          <option value="INTERMEDIATE">Intermediate</option>
          <option value="EXPERIENCED">Experienced</option>
        </select>
        <button onClick={handleSaveExperience}>Save Experience</button>
        {!user && (
          <p style={{ fontSize: "0.9em", color: "gray" }}>
            Login to save your experience permanently.
          </p>
        )}
      </div>

      {/* Message display */}
      {message && <p style={{ color: "green", marginTop: "10px" }}>{message}</p>}
    </div>
  );
}
